import React, { createContext, useState, useRef, useEffect } from 'react';
import { io } from 'socket.io-client';
import Peer from 'simple-peer';

import useSound from 'use-sound';

import beep from '../src/assets/sounds/beep.mp3';
import cheer from '../src/assets/sounds/cheer.mp3';
import fart from '../src/assets/sounds/fart.mp3';
import laugh from '../src/assets/sounds/laugh.mp3';
import scream from '../src/assets/sounds/scream.mp3';

const SocketContext = createContext();

// const socket = io('http://localhost:5000');
const socket = io('https://cpsc581-p2.herokuapp.com/');

const ContextProvider = ({ children }) => {
  const [callAccepted, setCallAccepted] = useState(false);
  const [callEnded, setCallEnded] = useState(false);
  const [stream, setStream] = useState();
  const [name, setName] = useState('');
  const [call, setCall] = useState({});
  const [me, setMe] = useState('');
  const [callerId, setCallerId] = useState('');
  const [book, setBook] = useState(-1);
  const [pageNumber, setPageNumber] = useState(0);

  const [started, setStarted] = useState(0);

  const [bookTitles, setBookTitles] = useState(["Homework Yuck!", "Hammy the Hamster"]);

  const myVideo = useRef();
  const userVideo = useRef();
  const connectionRef = useRef();

  const [playBeep] = useSound(beep);
  const [playCheer] = useSound(cheer);
  const [playFart] = useSound(fart);
  const [playLaugh] = useSound(laugh);
  const [playScream] = useSound(scream);

  useEffect(() => {
    // navigator.mediaDevices.getUserMedia({ video: true, audio: true })
    //   .then((currentStream) => {
    //     setStream(currentStream);

    //     myVideo.current.srcObject = currentStream;
    //   });

    socket.on('turnPage', (page) => {console.log(`Found Page: ${page}`); setPageNumber(page)});

    socket.on('changeBook', (bookId) => {console.log(`New book: ${bookId}`); setPageNumber(0); setBook(bookId)});

    socket.on('me', (id) => setMe(id));

    socket.on('playSfx', (sfx) => {
      console.log(`Hearing sound: ${sfx}`)
      if (sfx === 'beep'){
        console.log("in beep")
        playBeep();
      }else if (sfx === 'cheer'){
        playCheer();
      }else if (sfx === 'fart'){
        playFart();
      }else if (sfx === 'laugh'){
        playLaugh();
      }else if (sfx === 'scream'){
        playScream();
      }
    });

    socket.on('callUser', ({ from, name: callerName, signal, book: selectedBook }) => {
      console.log(`Client Book: ${selectedBook}`);
      setBook(selectedBook);
      setCall({ isReceivingCall: true, from, name: callerName, signal });
    });
  }, []);

  useEffect(() => {
    if (started !== 0){
      console.log("Started!!!")

      navigator.mediaDevices.getUserMedia({ video: true, audio: true })
        .then((currentStream) => {
          setStream(currentStream);

          myVideo.current.srcObject = currentStream;
        });
    }
    
  }, [started]);

  useEffect(() => {
    if (pageNumber > 0){
      console.log("Page Turned: "+pageNumber)
      turnPage(callerId);
    }
    
  }, [pageNumber]);

  useEffect(() => {
    if (book > -1){
      changeBook(book);
    }
    
  }, [book]);

  const answerCall = () => {
    setCallAccepted(true);

    const peer = new Peer({ initiator: false, trickle: false, stream });

    peer.on('signal', (data) => {
      console.log(call.from)
      setCallerId(call.from)
      socket.emit('answerCall', { signal: data, to: call.from });
    });

    peer.on('stream', (currentStream) => {
      userVideo.current.srcObject = currentStream;
    });

    peer.signal(call.signal);

    connectionRef.current = peer;
  };

  const turnPage = (id) => {
    console.log(`in turn page ${id}`);
    socket.emit('turnPage', { targetCaller: id, pageNumber: pageNumber });
  };

  const playSfx = (sfx) => {

    if (sfx === 'beep'){
      console.log("playing beep")
      playBeep();
    }else if (sfx === 'cheer'){
      playCheer();
    }else if (sfx === 'fart'){
      playFart();
    }else if (sfx === 'laugh'){
      playLaugh();
    }else if (sfx === 'scream'){
      playScream();
    }

    socket.emit('playSfx', { targetCaller: callerId, sfx });
  };

  const changeBook= () => {
    console.log(`changing book to: ${book} with user ${callerId}`);
    socket.emit('changeBook', { targetCaller: callerId, bookId: book });
  };

  const callUser = (id) => {
    setCallerId(id);
    const peer = new Peer({ initiator: true, trickle: false, stream });

    peer.on('signal', (data) => {
      socket.emit('callUser', { userToCall: id, signalData: data, from: me, name, book });
    });

    peer.on('stream', (currentStream) => {
      userVideo.current.srcObject = currentStream;
    });

    socket.on('callAccepted', (signal) => {
      setCallAccepted(true);

      peer.signal(signal);
    });

    connectionRef.current = peer;
  };

  const leaveCall = () => {
    setCallEnded(true);

    connectionRef.current.destroy();

    window.location.reload();
  };

  return (
    <SocketContext.Provider value={{
      call,
      callAccepted,
      myVideo,
      userVideo,
      stream,
      name,
      setName,
      callEnded,
      me,
      book,
      pageNumber,
      started,
      setStarted,
      setPageNumber,
      setBook,
      callUser,
      leaveCall,
      answerCall,
      turnPage,
      bookTitles, 
      setBookTitles,
      playBeep,
      playCheer,
      playFart,
      playLaugh,
      playScream,
      playSfx,
      
    }}
    >
      {children}
    </SocketContext.Provider>
  );
};

export { ContextProvider, SocketContext };
